import { Clicks } from '../data/Click'
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'y-nghia-truyen-co-tich-cay-tre-tram-dot-chia-se-cung-cac-ban-nho',
  data: () => ({
    anh: 'https://cotich.net/uploads/news/a_840726612_ynghiatruyencotichcaytretramdot4.jpeg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Ý nghĩa truyện cổ tích cây tre trăm đốt - Chia sẻ cùng các bạn nhỏ - Thư Viện',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_840726612_ynghiatruyencotichcaytretramdot4.jpeg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Ý nghĩa truyện cổ tích cây tre trăm đốt - Chia sẻ cùng các bạn nhỏ' },
      { name: 'Description', content: 'Ý nghĩa truyện cổ tích cây tre trăm đốt như thế nào và những bài học truyền đạt lại cho con cháu. Dưới đây là một số thông tin hữu ích nhất' },

      { itemprop: 'name', content: 'Thư Viện' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Thư Viện' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
    denmodau() {
      Clicks.clickmodau();
    },
    denketthuc() {
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

